.changeTech {
  position: fixed;
  bottom: 0;
  width: 100%;
  z-index: 2131;
  background: white;
  text-align: center;
  padding: 10px;
  border-top: 2px solid black;
  font-size: 18px;
}

.closeSel {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 21312;
  background: white;
   img {
     width: 24px;
     padding: 10px;
   }
  border-bottom: 2px solid gray;
  display: flex;
  justify-content: flex-end;
  align-items: center;

}

.changeTechWrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,.5);
  z-index: 21311;
  .changeTechBlock {
    background: white;
    width: 80%;
    margin: 0 auto;

    margin-top: 70%;
    h3 {
      text-align: center;
      padding: 10px 0;
    }
    p {
      padding: 10px 20px;
      border-bottom: 1px solid gray;
    }
  }
}