@import '../../styles/mix.scss';

.navWrapper {
  @include d-flex-r();
  flex-wrap: wrap;
  margin: 10px 0;
  justify-content: center;
  align-items: flex-start;
  li {
    width: 100px;
    text-align: center;
    border: 1px solid black;
    margin: 10px 5px;
    background: #e0e0e0;
    font-weight: 500;
    padding: 0;
    font-size: 14px;
    p:last-child{
      font-size: 14px;
      font-weight: 400;
    }
  }
}


.active {
  background: white !important;
  border-color: red !important;
}

.typeNavWrapper {
  @include d-flex-r();
  justify-content: center;
  font-size: 12px;
  li {
    text-align: center;
    border: none;
    width: 85px;
    margin: 10px 0;
    padding: 2px;
    p {
      font-weight: 400;
      font-size: 12px;
      color: black;
    }
  }


}

.count {
  font-size: 14px !important;
  font-weight: 500 !important;
}

.count2 {
  font-size: 14px !important;
  font-weight: 500 !important;
  color: white !important;
  span {
    color: white !important;
  }
}

.white {
  color: white !important;
}

.totalActive {
  box-shadow: 2px 5px 5px rgba(0,0,0,.5);
}

.createTask {
  @include d-flex-r();
  padding-top: 20px;
  justify-content: center;
  button {
    margin-left: 20px;
    padding: 5px 11px;
    background: red;
    border: none;
    border-radius: 7px;
    color: white;
    font-weight: 500;
    font-size: 20px;
  }
}

.myTasks {
  font-size: 12px !important ;
}